* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 10vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: ridge;
}

main, header {
    margin-left: 10vw;
    width: auto;
}

header > * {
    margin: auto;
}

main > * {
    height: 100vh;
    margin: auto;
    width: 50vw;
}

.nav-list{
    margin: 1rem;
    list-style: none;
}

.nav-item{
    margin: 5px;
    text-decoration: none;
}

.nav-link{
    text-decoration: none;
    color: black;
}

